<template>
  <div>
    <nav class="navbar navbar-dark sticky-top bg-dark flex-md-nowrap p-0">
      <a class="navbar-brand col-sm-3 col-md-2 mr-0" href="#">Vue出一個電商網站</a>
      <button class="navbar-toggler position-absolute d-md-none collapsed" type="button" data-bs-toggle="collapse"
        data-bs-target="#sidebarMenu" @click="CollapseToggle">
        <span class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse" id="collapseExample">
        <div class="card card-body">
          Some placeholder content for the collapse component. This panel is hidden by default but revealed when the
          user activates the relevant trigger.
        </div>
      </div>
      <input class="form-control form-control-dark w-100" type="text" placeholder="Search" aria-label="Search">
      <ul class="navbar-nav px-3">
        <li class="nav-item text-nowrap">
          <a class="nav-link" href="#" @click="Logout">登出</a>
        </li>
      </ul>
    </nav>
    <Alert />
    <div class="container-fluid">
      <div class="row">
        <nav id="sidebarMenu" class="col-md-3 col-lg-2 d-md-block bg-light sidebar collapse">
          <div class="position-sticky pt-3">
            <h6 class="
            sidebar-heading
            d-flex
            justify-content-between
            align-items-center
            px-3
            mt-4
            mb-1
            text-muted
          ">
              <span>管理員</span>
              <a class="link-secondary" href="#" aria-label="Add a new report">
                <span data-feather="plus-circle"></span>
              </a>
            </h6>
            <ul class="nav flex-column mb-2 nav nav-pills flex-column mb-auto">
              <li class="nav-item" @click="CollapseToggle">
                <router-link class="nav-link" to="/admin/products">
                  <i class="fas fa-box-open"></i>
                  商品列表
                </router-link>
              </li>
              <li class="nav-item" @click="CollapseToggle">
                <router-link class="nav-link" to="/admin/coupons">
                  <i class="fas fa-ticket-alt"></i>
                  優惠卷
                </router-link>
              </li>
              <li class="nav-item" @click="CollapseToggle">
                <router-link class="nav-link" to="/admin/orderlist">
                  <i class="fas fa-list"></i>
                  訂單列表
                </router-link>
              </li>
            </ul>
            <h6 class="
            sidebar-heading
            d-flex
            justify-content-between
            align-items-center
            px-3
            mt-4
            mb-1
            text-muted
          ">
              <span>模擬功能</span>
              <a class="link-secondary" href="#" aria-label="Add a new report">
                <span data-feather="plus-circle"></span>
              </a>
            </h6>
            <ul class="nav flex-column mb-2">
              <li class="nav-item" @click="CollapseToggle">
                <router-link class="nav-link" to="/admin/customer-orders">
                  <i class="fas fa-shopping-cart"></i>
                  模擬訂單
                </router-link>
              </li>
              <li class="nav-item">
                <a class="nav-link" href="https://emily-wang-0711.github.io/vue-client" target="_blank">
                  <i class="fas fa-store"></i>
                  購物前台
                </a>
              </li>
            </ul>
          </div>
        </nav>
        <main class="col-md-9 ml-sm-auto col-lg-10 px-4">
          <router-view></router-view>
        </main>
      </div>
    </div>
  </div>
</template>

<script>
  //   import NavBar from './NavBar';
  //   import SideBar from './SideBar';
  import $ from "jquery";
  import Alert from './AlertMessage';
  export default {
    components: {
      //   NavBar,
      //   SideBar,
      Alert,
    },
    methods: {
      Logout() {
        const api = `${process.env.APIPATH}/logout`;
        const vm = this;
        this.$http.post(api, vm.user).then((response) => {
          if (response.data.success) {
            vm.$router.push("/login");
          }
        });
      },
      CollapseToggle(){
           $("#sidebarMenu").toggle();
      }
    },
    created() {
      const myCookie = document.cookie.replace(
        /(?:(?:^|.*;\s*)hexToken\s*=\s*([^;]*).*$)|^.*$/,
        "$1");
      this.$http.defaults.headers.common.Authorization = myCookie;
    }
  };

</script>



