<template>
  <div>
    <loading :active.sync="isLoading" />
    <div class="mt-4 d-flex justify-content-between">
      <nav aria-label="breadcrumb">
        <ol class="breadcrumb bg-transparent pl-0 pb-0 pt-4">
          <li class="breadcrumb-item">管理員</li>
          <li class="breadcrumb-item active" aria-current="page">商品列表</li>
        </ol>
      </nav>
      <div class="text-right mt-2">
        <button class="btn btn-primary" type="button" data-bs-toggle="modal" data-bs-target="#productModal"
          @click="openModal(true)">
          建立新的產品
        </button>
      </div>
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120">分類</th>
          <th>產品名稱</th>
          <th width="120">原價</th>
          <th width="120">售價</th>
          <th width="80">是否啟用</th>
          <th width="150">編輯</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in products" :key="item.id">
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td class="text-right">{{ item.origin_price | Currency}}</td>
          <td class="text-right">{{ item.price | Currency }}</td>
          <td>
            <span v-if="item.is_enabled" class="text-success">啟用</span>
            <span v-else>未啟用</span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-ms" @click="openModal(false, item)">
              編輯
            </button>
            <button class="btn btn-outline-danger btn-ms" @click="openDelModal(item)">
              刪除
            </button>
          </td>
        </tr>
      </tbody>
    </table>
    <pagination @passFunction="getProductsList"></pagination>
    <!-- Modal -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
      aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>新增產品</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image">輸入圖片網址</label>
                  <input type="text" class="form-control" id="image" placeholder="請輸入圖片連結"
                    v-model="productsTmp.imageUrl" />
                </div>
                <div class="form-group">
                  <label for="customFile">或 上傳圖片
                    <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                  </label>
                  <input type="file" id="customFile" class="form-control" ref="files" @change="uploadFile()" />
                </div>
                <img
                  img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                  class="img-fluid" alt="" :src="productsTmp.imageUrl" />
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title">標題</label>
                  <input type="text" class="form-control" id="title" placeholder="請輸入標題" v-model="productsTmp.title" />
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category">分類</label>
                    <input type="text" class="form-control" id="category" placeholder="請輸入分類"
                      v-model="productsTmp.category" />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price">單位</label>
                    <input type="unit" class="form-control" id="unit" placeholder="請輸入單位" v-model="productsTmp.unit" />
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price">原價</label>
                    <input type="number" class="form-control" id="origin_price" placeholder="請輸入原價"
                      v-model="productsTmp.origin_price" />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price">售價</label>
                    <input type="number" class="form-control" id="price" placeholder="請輸入售價"
                      v-model="productsTmp.price" />
                  </div>
                </div>
                <div class="form-group">
                  <label for="title">庫存</label>
                  <input type="number" class="form-control" id="num" placeholder="請輸入數量" v-model="productsTmp.num" />
                </div>

                <hr />

                <div class="form-group">
                  <label for="description">產品描述</label>
                  <textarea type="text" class="form-control" id="description" placeholder="請輸入產品描述"
                    v-model="productsTmp.description"></textarea>
                </div>
                <div class="form-group">
                  <label for="content">說明內容</label>
                  <textarea type="text" class="form-control" id="content" placeholder="請輸入產品說明內容"
                    v-model="productsTmp.content"></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input class="form-check-input" type="checkbox" id="is_enabled" v-model="productsTmp.is_enabled"
                      :true-value="1" :false-value="0" />
                    <label class="form-check-label" for="is_enabled">
                      是否啟用
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">
              取消
            </button>
            <button type="button" class="btn btn-primary" @click="updateProduct">
              確認
            </button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel"
      aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span>刪除產品</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            是否刪除
            <strong class="text-danger">{{ productsTmp.title }}</strong>
            商品(刪除後將無法恢復)。
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">
              取消
            </button>
            <button type="button" class="btn btn-danger" @click="DelProduct()">
              確認刪除
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import $ from "jquery";
  import Pagination from "../Pagination";
  export default {
    components: {
      Pagination,
    },
    data() {
      return {
        products: [],
        productsTmp: {},
        pagination: {},
        isNew: false,
        isLoading: false,
        status: {
          fileUploading: false,
        },
      };
    },
    methods: {
      getProductsList(page = 1) {
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMAPIPATH}/admin/products?page=${page}`;
        const vm = this;
        vm.isLoading = true;
        this.$http.get(api).then((response) => {
          vm.isLoading = false;
          vm.products = response.data.products;
          this.$bus.$emit("page:change", response.data.pagination);
        });
      },
      openModal(isNew, item) {
        $("#productModal").modal("show");
        if (isNew) {
          this.isNew = true;
          this.productsTmp = {};
        } else {
          this.productsTmp = Object.assign({}, item);
          this.isNew = false;
        }
      },
      updateProduct() {
        let api = `${process.env.APIPATH}/api/${process.env.CUSTOMAPIPATH}/admin/product`;
        let httpMethod = "post";
        const vm = this;
        if (!vm.isNew) {
          api = `${process.env.APIPATH}/api/${process.env.CUSTOMAPIPATH}/admin/product/${vm.productsTmp.id}`;
          httpMethod = "put";
        }
        this.$http[httpMethod](api, {
          data: vm.productsTmp
        }).then((response) => {
          if (response.data.success) {
            vm.$bus.$emit("message:push", response.data.message, "success", 2);
            $("#productModal").modal("hide");
            vm.getProductsList();
          } else {
            $("#productModal").modal("hide");
            vm.getProductsList();
            vm.$bus.$emit("message:push", response.data.message, "danger", 2);
          }
        });
      },
      openDelModal(item) {
        $("#delProductModal").modal("show");
        this.productsTmp = Object.assign({}, item);
      },
      DelProduct() {
        const vm = this;
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMAPIPATH}/admin/product/${vm.productsTmp.id}`;
        this.$http.delete(api).then((response) => {
          vm.$bus.$emit("message:push", response.data.message, "success", 2);
          $("#delProductModal").modal("hide");
          vm.getProductsList();
        });
      },
      uploadFile() {
        const vm = this;
        vm.status.fileUploading = true;
        const uploadFile = this.$refs.files.files[0];
        const formData = new FormData();
        formData.append("file-to-upload", uploadFile);
        vm;
        const api = `${process.env.APIPATH}/api/${process.env.CUSTOMAPIPATH}/admin/upload`;
        this.$http
          .post(api, formData, {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          })
          .then((response) => {
            if (response.data.success) {
              vm.status.fileUploading = false;
              vm.$set(vm.productsTmp, "imageUrl", response.data.imageUrl);
            } else {
              vm.$bus.$emit("message:push", response.data.message, "danger", 3);
            }
          });
      },
    },
    created() {
      this.getProductsList();
    },
  };

</script>



